/**

    Google Fonts
    Lato, Roboto, Poppins, Madimi One, Montserrat, Angkor, Oswald


**/
@import url("https://fonts.googleapis.com/css2?family=Angkor&family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Madimi+One&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Oswald:wght@200..700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap");
:root {
  /* 
  ## Colors

  ### Primary */
  --clr-primary-blue: hsl(246, 80%, 60%);
  --clr-primary-light-red-work: hsl(15, 100%, 70%);
  --clr-primary-soft-blue-play: hsl(195, 74%, 62%);
  --clr-primary-light-red-study: hsl(348, 100%, 68%);
  --clr-primary-lime-green-exercise: hsl(145, 58%, 55%);
  --clr-primary-violet-social: hsl(264, 64%, 52%);
  --clr-primary-soft-orange-self-care: hsl(43, 84%, 65%);
  /* ### Neutral */
  --clr-neutral-very-dark-blue: hsl(226, 43%, 10%);
  --clr-neutral-dark-blue: hsl(235, 46%, 20%);
  --clr-neutral-desaturated-blue: hsl(235, 45%, 61%);
  --clr-neutral-pale-blue: hsl(236, 100%, 87%);
  --clr-white: #fff;
}

body {
  background-color: var(--clr-neutral-very-dark-blue);
  color: var(--clr-white);
}

body {
  font-size: 1.125rem;
  font-family: "Rubik", sans-serif;
}

h1 {
  font-size: 1.5rem;
  line-height: 1;
  font-weight: 300;
  margin: 0;
}
@media (min-width: 1100px) {
  h1 {
    font-size: 2.5rem;
  }
}

h2 {
  font-size: 1.125rem;
  font-weight: 400;
}

p {
  font-size: 0.9375rem;
  font-weight: 300;
  color: var(--clr-neutral-pale-blue);
}

a {
  color: var(--clr-neutral-desaturated-blue);
  text-decoration: none;
  font-size: 1.125rem;
}
a:hover, a.active {
  color: var(--clr-white);
}

h2,
p {
  padding: 0;
  margin: 0;
}

@media (min-width: 1100px) {
  body {
    min-height: 100vh;
    display: grid;
    place-content: center;
  }
}

.dashboard {
  padding: 5.125rem 1.5rem;
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}
@media (min-width: 768px) {
  .dashboard {
    max-width: 1100px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 2rem;
    row-gap: 4rem;
    margin-left: 2rem;
  }
  .dashboard .dashboard__header {
    grid-row: 1/4;
    grid-column: 1/2;
  }
}
@media (min-width: 1100px) {
  .dashboard {
    max-width: 1100px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 2rem;
    row-gap: 4rem;
    margin-left: 2rem;
  }
  .dashboard .dashboard__header {
    grid-row: 1/3;
    grid-column: 1/2;
    height: calc(100% + 36px);
    margin-top: -36px; /*card :before height*/
  }
}

.dashboard__header {
  background-color: var(--clr-neutral-dark-blue);
  border-radius: 0.9375rem;
}
.dashboard__header .dashboard__profile {
  border-radius: 0.9375rem;
  background-color: var(--clr-primary-blue);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  text-align: left;
}
@media (min-width: 768px) {
  .dashboard__header .dashboard__profile {
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    gap: 3rem;
    padding-bottom: 6rem;
  }
  .dashboard__header .dashboard__profile h1 {
    padding: 1rem 0;
  }
}
.dashboard__header .dashboard__profile img {
  max-width: 64px;
  border-radius: 50%;
  border: 3px solid var(--clr-white);
}
@media (min-width: 1100px) {
  .dashboard__header .dashboard__profile img {
    max-width: 78px;
  }
}
.dashboard__header .dashboard__nav {
  padding: 1.5rem 2rem;
}
.dashboard__header .dashboard__nav ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding-inline-start: 0;
}
@media (min-width: 768px) {
  .dashboard__header .dashboard__nav ul {
    flex-direction: column;
    gap: 2rem;
  }
}

.card {
  padding: 1.5rem;
  background-color: var(--clr-neutral-dark-blue);
  border-radius: 0.9375rem;
  position: relative;
}
.card .card__body {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}
@media (min-width: 1100px) {
  .card .card__body {
    grid-template-rows: 1fr 1fr 1fr;
  }
}
.card .card__body .card__title {
  grid-row: 1/2;
  grid-column: 1/2;
  align-self: flex-end;
}
@media (min-width: 768px) {
  .card .card__body .card__title {
    align-self: center;
  }
}
.card .card__body .card__time {
  grid-row: 2/3;
  grid-column: 1/2;
  font-size: 2rem;
  color: var(--clr-white);
}
@media (min-width: 1100px) {
  .card .card__body .card__time {
    font-size: 3.5rem;
    grid-column: 1/3;
  }
}
.card .card__body .card__link {
  grid-row: 1/2;
  grid-column: 2/3;
  text-align: right;
  align-self: flex-end;
}
@media (min-width: 768px) {
  .card .card__body .card__link {
    align-self: center;
  }
}
.card .card__body .card__last {
  grid-row: 2/3;
  grid-column: 2/3;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
@media (min-width: 1100px) {
  .card .card__body .card__last {
    grid-row: 3/4;
    grid-column: 1/3;
    justify-content: flex-start;
  }
}
.card[data-title="Self Care"], .card[data-title=Social], .card[data-title=Exercise], .card[data-title=Study], .card[data-title=Play], .card[data-title=Work] {
  margin-top: 2.25rem;
}
@media (min-width: 1100px) {
  .card[data-title="Self Care"], .card[data-title=Social], .card[data-title=Exercise], .card[data-title=Study], .card[data-title=Play], .card[data-title=Work] {
    margin-top: 0;
  }
}
.card[data-title="Self Care"]::before, .card[data-title=Social]::before, .card[data-title=Exercise]::before, .card[data-title=Study]::before, .card[data-title=Play]::before, .card[data-title=Work]::before {
  content: "";
  background-position: right 10px top -10px;
  background-repeat: no-repeat;
  left: 0;
  top: -36px;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  border-radius: 0.9375rem;
}

.card[data-title=Work]::before {
  background-image: url("../../assets/images/icon-work.svg");
  background-color: var(--clr-primary-light-red-work);
}

.card[data-title=Play]::before {
  background-image: url("../../assets/images/icon-play.svg");
  background-color: var(--clr-primary-soft-blue-play);
}

.card[data-title=Study]::before {
  background-image: url("../../assets/images/icon-study.svg");
  background-color: var(--clr-primary-light-red-study);
}

.card[data-title=Exercise]::before {
  background-image: url("../../assets/images/icon-exercise.svg");
  background-color: var(--clr-primary-lime-green-exercise);
}

.card[data-title=Social]::before {
  background-image: url("../../assets/images/icon-social.svg");
  background-color: var(--clr-primary-violet-social);
}

.card[data-title="Self Care"]::before {
  background-image: url("../../assets/images/icon-self-care.svg");
  background-color: var(--clr-primary-soft-orange-self-care);
}